.grid {
    display: grid;
    grid-gap:100px;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    align-content: center;
    justify-items: center;
    margin: 50px 40px;
}
.project {
    transition: transform .1s;

}

.project:hover {
    transform: scale(1.15);
}
img {
    border-radius: 10px;
    opacity: .8;
}
.project:hover img {
    opacity: 1;
}


/* Hide the text and appear on hover */

.expand-links {
    visibility: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    padding: 1em;
    font-size: 1.4em;
    background-color:rgba(17, 172, 180, 0.712);
    font-weight: 700;
    
    transition: ease-in-out .1s;
}
.expand-links:hover {
    background-color: rgba(17, 172, 180, 0.993);
}
.project:hover .expand-links {
    visibility: visible;
}
a {
    color: white;
    transition: ease-in-out 90ms;
}
a:hover {
    color:rgb(79, 223, 228)
}
span {
    color: rgb(79, 223, 228)
}

.tip-bar {
    margin-top: 20px;
    text-align: center;
}