.about {
    margin: 50px 40px;
}
.about-me {
    padding: 1.5em;
    border-radius: 10px;
    background-color: rgba(17, 172, 180, 0.712);

}

.btn {
    padding: .3em .8em;
    color: rgb(255, 255, 255);
    background-color: rgb(108, 226, 255);
    border: none;
    border-radius: 10px;
    margin-top: 20px;
}

.github{
    margin-left: 10px;
}

.line {
    border-top: white 1px solid;
    margin: 25px
}

/* insert wave someday when you know how to fill the rest of the body */

.skills {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    grid-gap: 20px;
    margin: 0 50px;

}
.filled {
    color: rgb(108, 226, 255)
}
.not-filled {
    color: white;
}
.skill {
    background:  rgba(17, 172, 180, 0.712);
    padding: 1em;
}

