nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-top: 1em;
}
.nav-links {
    display: flex;
    justify-content: space-around;
    width: 40%;
 
}


li {
    list-style: none;
    font-size: 1.4em;
    font-weight: 700;
    position: relative;
    transition: 30ms ease-in-out;
    color: white;
}

a {
    text-decoration: none;
    transition: 90ms ease-in-out
}


li:hover {
    color: rgb(79, 223, 228);
}

@media(max-width: 900px) {
  .nav-links {
    width: 100%;
  }
}